$body-font:'Open Sans', sans-serif;
$body-fontSize: 1.5rem;

body {
    height:100%;
    background-color: #fcfcfc;
    font-family: $body-font;
    font-size: $body-fontSize;
}

$heading-color: #5a898f;
$heading-font: 'Playfair Display', serif;

$secHeading-color:#fcfcfc;
$secHeading-font:'Oswald', sans-serif;
$secHeading-font-weight: 400;


h1{
    color: $heading-color;
    font-family: $heading-font;
    padding-top:5%;
    margin-left:2rem;
}


h2,h3,{
  color: $secHeading-color;
  font-family: $secHeading-font;
  font-weight: $secHeading-font-weight;
}

h4,h5, {
  font-family: $secHeading-font;
}

main {
  padding-left: 300px;
}

    @media only screen and (max-width : 992px) {
      main {
        padding-left: 0;
      }
    }

  img.bbLogo{
    width: 30%;
    margin-left:2rem;
  } 
  
  header.App-header {
    background-color: #5a898f;
    position:fixed;
    z-index: 2;
    top:0;
    width:100%;
  }

  section {
    padding-top:9%;
    background: #a3e2ea;
    bottom:0;
  }





@import './partials/Cart';
@import './partials/FeaturedItems';
@import './partials/Nav';
@import './partials/ProductList';
@import './partials/Search';
@import './partials/DetailsPage';